.cartTotalBlock{
    ul{
        display: block;
        margin-bottom: 40px !important;
        li{
            display: flex;
            align-items: flex-end;
            margin-bottom: 20px;
        }
        div{
            flex: 1;
            height: 1px;
            border-bottom: 1px dashed #dfdfdf;
            position: relative;
            top: -4px;
            margin: 0 7px;
        }
    }
    .greenButton{
        position: relative;

        &:disabled{
          animation: button-loading 0.6s ease-in-out infinite;
        }

        &:hover{
            img{
               transform: translateX(5px); 
            }
        }
       img{
           position: absolute;
           right: 30px;
           top: 20px;
           transition: transform 0.15s ease-in-out;
       } 
    }
}

.greenButton{
        width: 100%;
        height: 55px;
        background: #d41a1a;
        border-radius: 18px;
        border: 0;
        color: #fff;
        font-size: 16px;
        font-weight: 500;
        cursor: pointer;
        transition: background 0.1s ease-in-out ;
        &:disabled{
          background-color: #bebebe !important;
          cursor: default;
        }
        &:hover{
            background: lighten( #817474, 5%);
        }
        &:active{
            background: darken(#817474, 5%);
        }
}


.cartEmpty {
  text-align: center;
  
  p {
    width: 280px;
    line-height: 24px;
  }
  
  .greenButton {
    width: 245px;
    margin-top: 20px;
    
    &:hover {
      img {
        transform: rotate(180deg) translateX(3px);
      }
    }
    
    img {
      position: relative;
      top: 1px;
      transform: rotate(180deg);
      margin-right: 15px;
      transition: transform 0.15s ease-in-out;
    }
  }
}

.cartItem{
  border: 1px solid #f3f3f3;
    border-radius: 20px;
    overflow: hidden;
    padding: 20px;
    .cartItemImg{
      height: 70px;
      width: 70px;
      background-size: contain;
      background-position: 0 px;
      background-repeat: no-repeat;
      margin-right: 20px;
    }
    
    p{
      font-size: 16px;
      margin: 0;
    }
    b{
      font-size: 14px;
    }
    .removeBtn{
      opacity: 0.5;
      cursor: pointer;
      transition: opacity 0.15s ease-in-out;
      &:hover{
        opacity: 1;
      }
    }
  }
  
  @keyframes button-loading {
    0%{
      opacity: 1;
    }
    50%{
      opacity: 0.7;
    }
    100%{
      opacity: 1;
    }
  }
  